.section_title{
    color: var(--primary-color);
    font-size: 2.5rem;
    font-weight: 600;
    padding-bottom: 30px;
    }
    
    .product_item{
        cursor: pointer;
    }
    
    .card_container{
        display:flex;
        flex-direction: row;
        column-gap: 50px;
        justify-content: space-around;
    }
    
    .product_info h3{
        font-size: 1.1rem;
        color: var(--primary-color);
        font-weight: 600;
        margin-top: 15px;
        text-align: center;
        padding-bottom: 10px;
    }
    
    .product_info span {
        font-size: 0.9rem;
    }
    
    .product_card-bottom {
        display:flex;
        flex-direction: row;
        justify-content: center;
    }
    .price{
        color: var(--primary-color);
        font-size: 1.5rem;
        font-weight: 500;
        padding-right: 10px;
    }
    
    #icon{
        font-size: 1.2rem;
        padding: 5px;
        background: var(--primary-color);
        color: #fff;
        border-radius: 50px;
    }
    
    