
.filter_widget select {
  padding: 8px 10px;
  border: 1px solid var(--primary-color);
  cursor: pointer;
  border-radius: 5px;
  background-color: var(--primary-color);
  color: #fff
}

.filter_widget select:focus{
    outline: none !important;
}

.filter_widget select option {
    font-size: 0.9rem;
}

.search_box {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    border: 1px solid var(--primary-color);
    border-radius: 5px;
    padding-right: 12px;
    padding-left: 2px;
}

.search_box input {
    width: 100%;
    border: none;
    outline: none;
    padding: 8px 10px;
}

.search_box span {
    color: var(--primary-color)
}

.nothing-found{
    color: black !important;
    text-align: center;
    font-size: 2.5rem;
    font-weight: 600;
}

@media only screen and (max-width: 768px) {
    .filter_widget {
        margin-bottom: 30px;
    }

    .filter_widget select  {
        padding: 7px 20px;
    }
}