.service_item{
    margin-top: 5px;
padding: 20px;
background: var(--card-bg-01);
display: flex;
align-items: center;
column-gap: 0.7rem;
border-radius: 5px;
}

.service_item span i{
font-size: 2.2rem;
background: var(--primary-color);
padding:10px;
border-radius: 50px;
color: #fff;
font-weight: 400 !important;
}

.service_item h3 {
color: var(--primary-color);
font-size: 1.1rem;
font-weight:500;
}

.service_item p{
    font-size: 0.9rem;
    margin-top:5px;
    color: #222;
}